.header {
    background: $off-black;
    color: white;
    margin-bottom: $m-size;
    padding: $m-size 0;
  }
  
  .header__title {
    font-size: $l-size;
    margin: 0;
  }
  
  .header__subtitle {
    color: $off-white;
    font-size: $m-size;
    font-weight: 500;
    margin: 0;
  }

  // for desktop if for mobile we use max-width
  @media(min-width:45rem){
.header{
  margin-bottom: $xl-size;

}
  }
  